body {
  font-size: 15px;
  background: $offwhite;
}

a,
a:visited {
  color: $purple;

  @include transition();
}

a:hover {
  color: $altpurple;
}

.nav > li {
  font-size: 1rem;
  letter-spacing: -0.03em;

  a,
  a:visited {
    padding: 0.5rem 0.85rem;
    display: inline-block;
  }
}

.p--primary {
  font-size: 1rem;
}

.h3--primary {
  font-size: 1.5rem;
}

.h4--primary {
  font-size: 1.25rem;
}

.h5--primary {
  font-size: 1.1rem;
}

h1.single-title {
  font-family: $font-headings;
  text-transform: uppercase;
  font-size: 4rem;
  line-height: 1.2em;
}

h2.single-subtitle {
  font-family: $font-headings;
  font-size: 1.8rem;
  margin: 1rem 0 0.5rem;
  opacity: 0.85;
}




